* {
  margin:0;
  padding:0;
  box-sizing: border-box;
}
body{
  font-family: 'montseratt' , sans-serif;
}

.app{
  background-image:  url('./assets/cold-bg.jpeg');
  background-size: cover;
  background-position: bottom;
  transition: 0.4s ease-out;
  
}
.app.warm{
  background-image:  url('./assets/warm-bg.jpeg');
  transition: 0.4s ease-out;
}
main {
  min-height: 100vh ;
  padding: 25px;
  background-image: linear-gradient(to bottom, rgba(0,0,0,0.2), rgba(0,0,0,0.75));
}
.searchbpx{
  width: 100%;
  margin: 0 0 75px;
}

.searchbar{
  display: block;
  width: 100%;
  padding: 15px;
  appearance: none;
  background: none;
  border: none;
  outline: none;

  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 0px 0px 8px 8px;
  margin-top: -20px;

  box-shadow: 0px 5px rgba(0, 0, 0, 0.2);
  color: #313131;
  font-size: 20px;
  transition: 0.2s ease-out;

}

.searchbox .searchbar:focus{
  background-color: rgba(255, 255, 255, 0.8);
}

.locationbox .location{
  color: #fff;
  margin-top: 20px;
  font-size: 32px;
  font-weight: 500;
  text-align: center;
  text-shadow: 3px 3px rgba(50, 55, 55, 0.5);
}

.locationbox .date{
  color: #fff;
  font-size: 20px;
  font-weight: 300;
  font-style: italic;
  text-align: center;
  text-shadow: 2px 2px rgba(50, 55, 55, 0.5);
}
.weatherbox{
  text-align: center;
  margin-top: 20px;
}
.weatherbox .temp{
  position: relative;
  display: inline-block;
  margin: 30px auto;
  background-color: rgba(255, 255, 255, 0.5);
  border-radius: 16px;
  padding: 15px 25px;
  color: #fff;
  font-size: 100px;
  font-weight: 900;

  text-shadow: 3px 6px rgba(50, 55, 55, 0.5);
  text-align: center;
  box-shadow: 3px 6px rgba(0, 0, 0, 0.2);
}

.weatherbox .weather{
  color: #fff;
  font-size: 48px;
  font-weight: 700;
  text-shadow: 3px 3px rgba(255, 255, 255, 0.5);
}